@import url(https://fonts.googleapis.com/css2?family=Lato&family=Quicksand&display=swap);
h1,h2,h3,h4,h5,h6{
    font-family: 'Quicksand', sans-serif;
}

table .buttonsDesktop{
    display: block;
}

.myForm {
    min-width: 500px;
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.Modal{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0px;
    display: flex;
    flex-direction: column;
    align-items:center;
    background-color: rgba(7, 7, 7, 0.596);
    z-index: 2000;
    transition: all 1s ease;
    padding: 50px 0px 0px 0px;
}

.Modal .modal-dialog{
    width: 80%;
    position: relative;
    top:-1000px;
    transition: all .7s ease;
    height: auto!important;
    background-color: white;
    padding: 10px 16px 10px 16px;
    border-radius: 13px;
}

.Modal .modal-content{
    border:none!important;
}

.Modal .modal-dialog.show{
    top: 0px;
}

.Modal .modal-dialog i{
    position: absolute;
    top: -25px;
    background-color: #FFB347;
    right: -22px;
    padding: 10px;
    color: white;
    border-radius: 11px;
}

.Modal .modal-dialog{
    max-width: 55%!important;
}

@media(max-width:768px){
    .Modal .modal-dialog{
        max-width: 100%!important;
        height: 85vh!important;
    }
    .Modal .modal-dialog .modal-content{
        height: 100%;
    }
}
.table-envios tr.pago{
    background-color: #19578a;
    color: #FAFAFA;
}

.table-envios tr.nopago{
    background-color: #535252;
    color: white;
}
.table-envios thead, .table-envios tbody tr {
    display: table;
    width: 100%;
    table-layout: fixed;
}

.table-envios td, .table-envios th{
    border:none!important
}

.table-envios tr{
    border-radius:16px;
    box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.25);
    margin: 20px 0px;
}

.table-envios tbody {
    display: block;
    height: 568px;
    overflow: auto;
    padding: 10px;
}

@media(max-width:768px){
    .table-envios tbody{
        height: calc(100vh - 200px);
    }
    .table-envios thead{display: none!important;}

    .table-envios thead, .table-envios tbody tr {
        display: grid;
    }
    
    .table-envios .buttonsDesktop{
        display: flex;
        justify-content: space-around;
    }

    .table-envios .span-pagado{
        position: relative;
        top: 50px;
        display:block;
        width:10px;
        height:10px;
        background-color:#439443;
        border-radius:5px;
    }
}

.detalleVenta{
    overflow-y: auto;
}
.detalleVenta ul{
    border-bottom:solid 3px #FFB347;
    list-style: none;
}

.datosComprador{
    display: flex;
    align-items: center;
}

.datosComprador p{
    margin: 0;
    font-size: 23px;
    font-family: 'Quicksand', sans-serif;
}

.detalleVenta .datosComprador .fotoUsuario{
    height: 50px;
    border-radius: 25px;
}

.detalleVenta span{font-weight: bold;}

@media(max-width:768px){
    .datosComprador{
        justify-content: space-between;
    }
    .detalleVenta h3{font-size: 20px;}
    .detalleVenta ul{
        font-size: 13px;
        padding-left: 0px!important;
    }

    .detalleVenta table{font-size: 13px;}
}
#btn-mostrarFiltro{
    display: none;
}
@media(max-width:768px){
    #formFiltroEnvios{
        transition: all 1s ease;
        display: none;
    }

    #formFiltroEnvios.show{
        display: block;
    }
    #formFiltroEnvios section{
        margin:15px 0px
    }

    #btn-mostrarFiltro{
        display: block;
        position: absolute;
        right: 28px;
        top: 86px;
    }
}
